import React, { useEffect, useState } from "react";



import { getStore, updStore, gen16CodeId, compareJSON, fetchEncryptStream, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import SaveAlert from "./saveAlert";
import { SelectDropAccount_Cmpt } from "../../ComprobanteParametrizados";

const TextInput = TextInput_Cmpt();

const DropInput = SelectDropAccount_Cmpt();

const CheckBox = CheckBox_Cmpt();

const frmNm = "upd_comprobantebyId"

const UpdComprobante  = ({data, confirm, item}) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};



	const save  = async ()  => {
		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck"));
		
		let bdyq2 = {...getStore(qryStore())?.upd_opera};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId,
			operationId: item["operationId"],
		}
		bdyq2.data2update=diff.data;
		
		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
		
	};



	let flds = ["document","amount","type","description", "account", "subAccount", "comprobanteId"]

	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )




	
	
	const OpenSaveAlert =  () => {
		let lastTimeCallModal = getStore("lastTimeCallModal") || 0;
		if(Date.now() > lastTimeCallModal){
			updStore("lastTimeCallModal", Date.now()+320);
			
			let modaldata = {};
			modaldata["zIndex"] = 450;
			modaldata["height"] = "40vh";
			// data['observeResize']=true;
			modaldata["props"] = { minHeight: "1vh", overlay: true, closeEvent: ()=>{}  };
			modaldata["content"] = <SaveAlert confirm={save} item={item} />
			OpenModal(modaldata);
		}
	}

	const handlePick = (acc,subacc) => {
		updFrm("account",acc);
		updFrm("subAccount",subacc)
	}
	

	return (
		<div
		style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}
		>
			<div className="_dsplFlx ">
				<p className="form_title">
					Actualizar Operacion 
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx ">
				<TextInput  
					init={getStore(frmNm)?.["document"]} 
					label={"Documento"} 
					width={120} 
					updChanges={(e)=>updFrm("document",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["amount"]} 
					label={"Importe"} 
					width={120} 
					type={"number"}
					updChanges={(e)=>updFrm("amount",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["comprobanteId"]} 
					label={"comprobanteId"} 
					width={120} 
					updChanges={(e)=>updFrm("comprobanteId",e)}
				/>
			</div>
			
			<TextInput  
				init={getStore(frmNm)?.["description"]} 
				label={"Descripcion"} 
				width={520} 
				updChanges={(e)=>updFrm("description",e)}
			/>

			<div className="_dsplFlx " style={{padding: "18px"}}>
				<div className="flexSpace"/>
				<CheckBox 
					data={[
						{label: "Deudoras", id:"debit"},
						{label: "Acredoras", id:"credit"}
						
					]}
					active={getStore(frmNm)?.["type"]}
					updSelect={(e)=>updFrm("type",e)}
				/>
				<div className="flexSpace"/>
			</div>
			{getStore(frmNm)?.account?
			<div className=" acc_dtls">			
					<div className="_dsplFlx"  >
						<div className="code" style={{color: "#646cff"}} onClick={()=>updFrm("account",null)}>
							{getStore("account_ids")?.[getStore(frmNm)?.account]?.code} 
						</div>
						<div className="name">
						{getStore("account_ids")?.[getStore(frmNm)?.account]?.name}
						</div>
					</div>
					<div className="flexSpace"/>
					{getStore(frmNm)?.subAccount ?
					<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						<div className="code" >
							{getStore("sub_account_ids")?.[getStore(frmNm)?.subAccount]?.subAccount} 
						</div>
						<div className="name">
						{getStore("sub_account_ids")?.[getStore(frmNm)?.subAccount]?.subAccountName} 
						</div>
					</div>
					:null}
				</div>
				:
				<DropInput
					width={420}
					handleClick={handlePick}
				/>}
			

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				{diff?.hasChange ?
				<div className="card">
					<button onClick={OpenSaveAlert}>
						Actualizar
					</button>
				</div>
				:null}
			</div>
		

			
		</div>
	);
};

export default UpdComprobante;
