
import React, { useState } from "react";

import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { TextInput_Cmpt } from "../../../components";
const TextInput = TextInput_Cmpt();




const SaveAlert  = ({ data, confirm, item, close }) => {
	const [code, setCode] = useState(0);
	const { modalID } = data;

	const handleClose =  (v) => {
		CloseModal({id:modalID})
    }
    
    const handleConfirm =  () => {
		
        confirm();
		handleClose();
	}



    return (
       
		<div style={{
			maxHeight: "40vh",
			minHeight:  "235px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Salvar cambios en el Comprobante
				</p>
				<div className="flexSpace"/>
				
			</div>
			<div className=" ">
				<span>
					{"Escriba el numero de comprobante"}
				</span>
				<span className={"comprobante_id_bold"}>
					{item.comprobanteId}
				</span>
				<span>
					{"para guardar los cambios"}
				</span>
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<TextInput  
					init={code} 
					label={"Comprobante"} 
					width={220} 
					updChanges={setCode}
				/>
				<div className="flexSpace"/>
			</div>
			<div className="_dsplFlx " style={{padding:"15px 10px"}}>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={handleClose}>
					Cancelar
				</p>
				{code===item.comprobanteId?
				<p className="group_title addBtn purple" onClick={handleConfirm}>
					Actualizar
				</p>
				:null}
			</div>

		</div>
    )   
}

export default SaveAlert;

